<template>
  <div style="text-align: center">
    <LoadingIndicator v-if="isLoading"></LoadingIndicator>
    <NoGames v-if="!isLoading && !jackpotGames"></NoGames>
    <JackpotGameComponent :games="jackpotGames"></JackpotGameComponent>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import GamesComponent from "../Games/GamesComponent";
  import JackpotGameComponent from "./JackpotGameComponent";
  import LoadingIndicator from "../Utilities/LoadingIndicator";
  import NoGames from "../Utilities/NoGames";

  export default {
    name: 'Jackpot',
    components: {
      NoGames, LoadingIndicator, JackpotGameComponent, GamesComponent},
    data () {
        return {
            isLoading: true
        }
    },
    mounted () {
      this.getJackpotGames().then(() => {
          this.isLoading = false;
      }, () => this.isLoading = false);
    },
    computed: {
      ...mapGetters(['jackpotGames'])
    },
    methods: {
      ...mapActions(['getJackpotGames'])
    }
  }
</script>
