<template>
  <div>
    <table class="bg-games mb-3 mb-xl-5 animated fadeIn">
      <tr
        v-for="game in games"
        :key="game.game_id"
        v-if="game && !$gameHelper.isStarted(game) &&
        $marketHelpers.getOddsForMarket(game, $marketHelpers.MARKET_TYPE.MATCH_RESULT).length &&
        $gameHelper.hasOdds($marketHelpers.getOddsForMarket(game,$marketHelpers.MARKET_TYPE.MATCH_RESULT))"
        class="match-row pb-2 pb-sm-0"
      >
        <td class="match-info text-left pt-4 pb-1 py-sm-3 pr-1 pl-4">
          <ul class="match">
            <li class="date pr-1">
              <span class="date__time">{{ $gameHelper.displayTime(game.scheduled)}}</span>
              <br>
              <span class="date__day">{{ $gameHelper.dateName(game.scheduled)}}</span>
            </li>
            <li class="pr-1">{{game.home_team}}</li>
            <li class="vs pr-1">vs</li>
            <li class="pr-1">{{game.away_team}}</li>
          </ul>
        </td>
        <td class="match-bets match-bets_1x2 py-2 py-sm-3 px-1">
          <div class="match-header">1 x 2</div>
          <ul class="name">
            <li
              class="mx-1 p-1"
              :class="[isSelected(game, odd) ? 'picked': '']"
              v-if="$marketHelpers.getOddsForMarket(game,$marketHelpers.MARKET_TYPE.MATCH_RESULT)"
              v-for="(odd, oddIndex) in $marketHelpers.getOddsForMarket(game,$marketHelpers.MARKET_TYPE.MATCH_RESULT)"
              :key="oddIndex"
              @click="addToJackpotSlip(game, odd)"
              style="cursor: pointer"
            >
              <span>{{odd.value}}</span>
            </li>
          </ul>
        </td>
        <td class="match-bets match-bets_all pt-4 py-sm-3 pl-1 pr-4"></td>
      </tr>
    </table>

    <!-- Form to save the jackpot selection to the api. -->
    <Table v-if="games">
      <tr>
        <td>
          <BetButton type="submit" @click="randomizeSelection()">Random Selection</BetButton>
        </td>

        <td>
          <!--Input for Stake-->
          <input
            type="text"
            placeholder="Stake"
            v-model="stake"
            class="transparent-input form-control rounded-0 stake search-form__text"
          >
          <span style="color: #8cc509; font-style: italic;" class="pt-1">At least sh. 100</span>
        </td>
        <td v-if="jackpotSlip">
          <BetButton type="submit" @click="clearSelection()">Clear Selection</BetButton>
        </td>
        <td>
          <BetButton
            type="submit"
            :disabled="!jackpotSlip || !stake || !authUser"
            @click="submitJackpot()"
          >Submit Jackpot</BetButton>
        </td>
      </tr>
    </Table>
  </div>
</template>

<script>
import moment from "moment";
import BetButton from "../Utilities/BetButton";
import Table from "../Utilities/Table";
import { SUBMIT__JACKPOT_URL } from "../../api_routes/index";
import { AUTH_CONFIG } from "../../auth_config/index";
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "JackpotGameRenderer",
  components: { BetButton, Table },
  props: ["games"],
  data() {
    return {
      stake: 100,
      picks: "",
      combinationsChanged: false
    };
  },
  mounted() {
    this.getJackpotSlip();
  },
  computed: {
    ...mapGetters(["jackpotSlip", "authUser"])
  },
  watch: {
    jackpotSlip() {
      this.getMaximumCombinations();
      this.$forceUpdate();
    }
  },
  methods: {
    ...mapActions(["getJackpotSlip"]),

    addToJackpotSlip(game, odd) {
      this.combinationsChanged = true;
      this.$store.dispatch("addToJackpotSlip", { game: game, odd: odd });
    },

    isSelected(game, odd) {
      let isSelected = false;
      let gamesInSlip = this.jackpotSlip;
      if (gamesInSlip) {
        gamesInSlip.forEach(g => {
          if (g.gameId === game.game_id && g.oddId === odd.id)
            isSelected = true;
        });
        return isSelected;
      }
    },

    randomizeSelection() {
      this.combinationsChanged = true;
      this.games.forEach(game => {
        let odds = this.$marketHelpers.getOddsForMarket(
          game,
          this.$marketHelpers.MARKET_TYPE.MATCH_RESULT
        );
        let randomOdd = odds[(Math.random() * odds.length) | 0];
        if (!this.$gameHelper.isStarted(game))
          this.addToJackpotSlip(game, randomOdd);
      });
    },

    clearSelection() {
      this.$store.dispatch("clearJackpotSlip");
      this.combinationsChanged = true;
    },

    submitJackpot() {
      let jackpotPicks = [];
      let jackpotSlipGames = this.jackpotSlip;
      jackpotSlipGames.splice(0, 15).forEach(game => {
        let pick = {
          game_id: parseInt(game.gameId),
          home_team: game.game.home_team,
          away_team: game.game.away_team,
          odd: game.oddValue,
          id: game.oddId,
          pick: game.oddOutCome,
          scheduled: moment(game.scheduled).format("YYYY-MM-DD HH:mm:ss"),
          market_id: game.market
        };
        jackpotPicks.push(pick);
      });

      let postData = {
        amount: this.stake,
        picks: jackpotPicks
      };
      if (this.stake < 100) {
        this.$noty.error("The stake should be at least 100");
      }
      axios
        .post(SUBMIT__JACKPOT_URL, postData, {
          headers: AUTH_CONFIG.getHeaders()
        })
        .then(
          resp => {
            let responseData = resp.data;
            if (responseData.code === 200) {
              this.$noty.success("Jackpot selections sent successfully");
              this.$store.dispatch("clearJackpotSlip");
              this.$store.dispatch("init_auth_user", { request: "refresh" });
            } else {
              this.$noty.error(
                "sorry, we are unable to submit your Jackpot selections, please contact our customer care"
              );
            }
          },
          () => {
            this.$noty.error(
              "sorry, we are unable to submit your Jackpot selections, please contact our customer care"
            );
          }
        );
    },

    getJackpotCombinations() {
      let currentJackpot = localStorage.getItem("jackpotSlip");
    },

    getMaximumCombinations() {
      let currentJackpot = this.jackpotSlip;
      let counts = [];

      if (currentJackpot) {
        currentJackpot.forEach(item => {
          counts.push(item.count);
        });
      }
      console.log("JACKPOT COMBINATIONS", counts);
      this.stake = Math.max.apply(null, counts) * 100;
    }
  }
};
</script>


